import { FC, useMemo } from 'react';
import { Helmet } from 'react-helmet';
import { useLocation } from 'react-router-dom';

const HelmetMetaData: FC<{
  quote?: string;
  title?: string;
  image?: string;
  description?: string;
  hashtag?: string;
}> = ({
  quote = '',
  title = 'Dave Kwiatkowski',
  image = '/logo512.png',
  description = '',
  hashtag = '#davekwiatkowski',
}) => {
  const location = useLocation();
  const currentUrl = useMemo(
    () => `https://davekwiatkowski.com${location.pathname}`,
    [location.pathname]
  );

  return (
    <Helmet>
      <title>{title}</title>
      <meta charSet='utf-8' />
      <meta http-equiv='X-UA-Compatible' content='IE=edge' />
      <meta name='csrf_token' content='' />
      <meta property='type' content='website' />
      <meta property='url' content={currentUrl} />
      <meta
        name='viewport'
        content='width=device-width, initial-scale=1, shrink-to-fit=no'
      />
      <meta name='msapplication-TileColor' content='#ffffff' />
      <meta name='msapplication-TileImage' content='/logo192.png' />
      <meta name='theme-color' content='#ffffff' />
      <meta name='_token' content='' />
      <meta name='robots' content='noodp' />
      <meta property='title' content={title} />
      <meta property='quote' content={quote} />
      <meta name='description' content={description} />
      <meta property='image' content={image} />
      <meta property='og:locale' content='en_US' />
      <meta property='og:type' content='website' />
      <meta property='og:title' content={title} />
      <meta property='og:quote' content={quote} />
      <meta property='og:hashtag' content={hashtag} />
      <meta property='og:image' content={image} />
      <meta content='image/*' property='og:image:type' />
      <meta property='og:url' content={currentUrl} />
      <meta property='og:site_name' content='Dave Kwiatkowski' />
      <meta property='og:description' content={description} />{' '}
    </Helmet>
  );
};

export default HelmetMetaData;
